section {
  border-bottom: 1.11111px solid #eaecef;
  max-width: 640px;
  font-family: -apple-system, BlinkMacSystemFont, Segoe UI, Helvetica, Arial, sans-serif, Apple Color Emoji, Segoe UI Emoji, Segoe UI Symbol;
  font-size: clamp(1.25rem, .96rem + 1.46vw, 2rem);
  font-weight: 600;
  display: block;
}

h2 {
  color: #14a05f;
  margin: 5px 0;
  font-family: cursive;
  font-size: clamp(1rem, .76rem + 1.22vw, 1.63rem);
}

.card-img {
  aspect-ratio: 6x4;
  object-fit: contain;
  width: 100%;
  max-width: 640px;
  height: auto;
  box-shadow: 0 3px 3px -2px #0003, 0 3px 4px #00000024, 0 1px 8px #0000001f;
}

.notice {
  text-shadow: .1px .3px .2px #757474;
  color: #000;
  opacity: 1;
  font-family: Courier New;
  transition: opacity 1s ease-in-out;
}

.container {
  aspect-ratio: 9x3;
  border-radius: 12px;
  flex-direction: column;
  align-items: center;
  width: 100%;
  max-width: 640px;
  height: auto;
  padding: 10px 0;
  display: flex;
  box-shadow: 0 3px 3px -2px #0003, 0 3px 4px #00000024, 0 1px 8px #0000001f;
}

.feedback-buttons {
  gap: 20px;
  display: flex;
}

.feedback-buttons button {
  cursor: pointer;
  background-color: #0000;
  border: none;
  border-radius: 12px;
  transition: box-shadow .15s ease-in;

  &:hover {
    box-shadow: 0 3px 3px -2px #0003, 0 3px 4px #00000024, 0 1px 8px #0000001f;
  }
}

.feedback-buttons img {
  width: 50px;
  height: 50px;
}

.diagram-container {
  aspect-ratio: 9x2;
  opacity: 0;
  justify-content: center;
  align-items: center;
  gap: 20px;
  width: 100%;
  max-width: 640px;
  max-height: 150px;
  transition: opacity 1s ease-in-out;
  display: none;
}

.diagram-container.show {
  opacity: 1;
  display: flex;
}

.diagram {
  aspect-ratio: 1;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 150px;
  height: 100%;
  display: flex;
  position: relative;
  box-shadow: 0 3px 3px -2px #0003, 0 3px 4px #00000024, 0 1px 8px #0000001f;
}

.diagram:before {
  content: "";
  border: 2px solid #020202;
  border-radius: 50%;
  position: absolute;
  inset: 0;
}

.total {
  aspect-ratio: 1;
  color: #fff;
  background-color: #000;
  border-radius: 50%;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 75px;
  font-size: clamp(.94rem, .48rem + 2.31vw, 1.69rem);
  display: flex;
  position: absolute;
}

.statistic-list {
  width: 250px;
  margin-left: 0;
  padding-left: 0;
  font-family: Arial;
  font-size: clamp(.63rem, .22rem + 2vw, 1.25rem);
}

.statistic-list li {
  justify-content: space-between;
  list-style-type: none;
  display: flex;
}

.category {
  font-weight: bold;
}

.dots {
  border-bottom: 1px dotted #000;
  flex-grow: 1;
  margin: 0 4px 2px;
}

.like-icon {
  aspect-ratio: 1;
  justify-content: center;
  align-items: center;
  width: 100%;
  max-width: 165px;
  display: flex;
  position: relative;
  overflow: hidden;
}

.heart-mask {
  width: 100%;
  height: 100%;
  position: absolute;
}

.heart-mask img {
  width: 100%;
  height: 100%;
}

.heart-fill {
  aspect-ratio: 100 / 92;
  transform-origin: bottom;
  z-index: -1;
  background-image: linear-gradient(#00ff6a, #54806f);
  background-repeat: no-repeat;
  background-size: 100% 100%;
  border-radius: 40% 40% 90% 90%;
  width: 87%;
  transition: transform .3s;
  position: absolute;
}

.percentage-text {
  color: #000;
  text-align: center;
  font-size: clamp(.94rem, .59rem + 1.73vw, 1.5rem);
  font-weight: bold;
  position: absolute;
  top: 50%;
  transform: translateY(-50%);
}

@media (width <= 640px) {
  .feedback-buttons img {
    width: 40px;
    height: 40px;
  }

  .diagram-container {
    gap: 10px;
    max-height: 120px;
  }

  .diagram {
    max-width: 125px;
  }

  .like-icon {
    max-width: 138px;
  }

  .total {
    max-width: 62px;
  }
}

@media (width <= 480px) {
  .feedback-buttons img {
    width: 35px;
    height: 35px;
  }

  .diagram-container {
    max-height: 95px;
  }

  .diagram {
    max-width: 100px;
  }

  .like-icon {
    max-width: 110px;
  }

  .total {
    max-width: 50px;
  }
}

@media (width <= 430px) {
  .diagram-container {
    max-height: 95px;
  }

  .diagram {
    max-width: 95px;
  }

  .total {
    max-width: 47px;
  }
}

@media (width <= 412px) {
  .diagram-container {
    max-height: 92px;
  }
}
